$(".burger").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})

$(".nav .menu-item-has-children > a").click(function () {
    if(window.innerWidth>992){
        return
    }
    if($(this).parents(".menu-item").hasClass("_active")){
        $(this).parents(".menu-item").removeClass("_active")
        $(this).siblings(".sub-menu").slideUp()
    }else{
        $(".menu-item").removeClass("_active")
        $(this).parents(".menu-item").addClass("_active")
        $(this).siblings(".sub-menu").slideDown()
    }
})

$(".header-search__btn").click(function () {
    $(".header-search").toggleClass("_show")
})

$(document).mouseup( function(e){
    let div = $(".header-search");
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_show")){
            $(div).removeClass("_show")
        }
    }
});
